import Vue from 'vue';
import 'babel-polyfill';
import Vuex from 'vuex';
import * as actions from './actions';
import * as getters from './getters';
import mutations from './mutations';

Vue.use(Vuex);

const debug = process.env.NODE_ENV !== 'production';

const state = {
  isLogin: false,
  contextData: {
    constantsNameValueMap: {},
    constantsValueMap: {},
    dictionaryMap: {},
    sysParamsMap: {},
    systemMode: {},
    user: {
      userId: '',
      userName: '',
      loginName: '',
      menuTree: [{resId: '', resCode: '', resName: '', resType: '', chlidren: []}]
    }
  },
  menus: [], // usermenuTreechecked == true 
  menusMap: {}, //  usermenuTree
  menusCodeMap: {}, // usermenuTree
  routerMap: {}, // 
  curMenu: {}, // 
  previousRouter: {}, //  
  curRouter: {}, //  
  msgTip: {}, // 
  showMobileNav: false // 
};

export default new Vuex.Store({
  state,
  actions,
  getters,
  mutations,
  strict: debug
});



