import Vue from 'vue';
import VueRouter from 'vue-router';
import routes from './routes.config';
import store from '../store';
import * as types from '../store/mutation-types';
import routerUtil from './router-util';

Vue.routesConfig = [];
let router = {instance: ''};

/**
 * 
 */
function initRouter (Vue) {
  Vue.use(VueRouter);
  Vue.routesConfig = [...Vue.routesConfig, ...routes];
  //  router 
  router.instance = new VueRouter({
    routes: Vue.routesConfig
  });

  // vuex
  store.commit(types.SET_ROUTER_MAP, routerUtil.getRouterMap(Vue.routesConfig));

  // home
  router.instance.beforeEach((to, from, next) => {
    if (!store.state.isLogin || store.state.isLogin === false) {
      store.commit(types.SET_PREVIOUS_ROUTER, from);
      if (to.path === '/admin/login') {
        store.commit(types.SET_CUR_ROUTER, to);
        Vue.api.core.getContextData().then(ret2 => {
          store.dispatch('setContextData', ret2);
          store.commit(types.SET_CUR_ROUTER, to);
          next();
        });
        next();
      } else {
        next('/admin/login');
      }
    }
  });
}

const install = function (Vue, opts = {}) {
  /* istanbul ignore if */
  if (install.installed) return;
  initRouter(Vue);// 
};

/* istanbul ignore if */
if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue);
}

// export const router = (() => {
//   return router.instance;
// })();

export default {
  install,
  router
};



